h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

body.modal-open {
  overflow: hidden;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

:root {
  --main-text-color: #2b2b2b;
  --second-title-color: #487996;
  --section-bgr-color: #e5e5e5;
  --menu-lang-color: #bdbdbd;
  --footer-bgr-color: #f5f5fa;
  --transition-function: 300ms cubic-bezier(.4, 0, .2, 1);
  --z-index-decor: 50;
}

body {
  font-family: Zen Maru Gothic, sans-serif;
}

.section {
  padding-top: 20px;
  padding-bottom: 20px;
}

@media screen and (min-width: 1440px) {
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

.button {
  width: 277px;
  height: 56px;
  color: var(--second-title-color);
  cursor: pointer;
  letter-spacing: .1em;
  text-transform: uppercase;
  transition: var(--transition-function);
  background-color: #f5f5fa;
  border: none;
  border-radius: 30.5px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  display: flex;
  box-shadow: -10px -10px 20px #fff, 10px 10px 20px rgba(72, 121, 150, .1), 5px 5px 10px rgba(72, 121, 150, .25), -5px -5px 10px #fff;
}

@media screen and (min-width: 768px) {
  .button {
    width: 277px;
  }
}

.button:hover, .button:focus {
  color: #f5f5fa;
  background-color: var(--second-title-color);
  box-shadow: -10px -10px 20px #fff, 10px 10px 20px rgba(72, 121, 150, .1), 5px 5px 10px rgba(72, 121, 150, .25), -5px -5px 10px var(--second-title-color);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.text {
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

@media screen and (min-width: 1440px) {
  .text {
    font-size: 18px;
    line-height: 1.28;
  }
}

.title {
  color: var(--second-title-color);
  font-size: 24px;
  font-weight: 700;
  line-height: 1.25;
}

@media screen and (min-width: 768px) {
