.mobile__open {
  width: 32px;
  height: 32px;
  transition: var(--transition-function);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  margin-right: 45px;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 768px) {
  .mobile__open {
    align-items: end;
    margin-right: 100px;
