.mobile__icon--open {
  fill: var(--second-title-color);
}

.phone {
  border: none;
  margin-right: 24px;
  padding: 0;
  display: inline-block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .phone {
    display: none;
  }
}

.phone__block--mobile {
  opacity: 0;
  width: 162px;
  z-index: 999;
  background-color: #fff;
  border: 1px solid #bdbdbd;
  border-radius: 0 0 20px 20px;
  padding: 5px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1), opacity .6s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: -100px;
  left: -70px;
  transform: translateY(0%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.phone__mobile:hover .phone__block--mobile, .phone__mobile:focus .phone__block--mobile {
  opacity: 1;
  transform: translateY(255%);
}

.phone__mobile:hover:before {
  opacity: 1;
  transform: translateY(300%);
}

.phone__mobile {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .phone__mobile {
    display: none;
  }
}

.phone__link:hover {
  color: var(--second-title-color);
  transition: color var(--transition-function);
}

.phone__mobile:before {
  content: "";
  width: 0;
  opacity: 0;
  border: 8px solid #fff;
  border-bottom-color: #bdbdbd;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1), opacity .6s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: -29px;
  left: 15%;
}

.phone__icon {
  fill: #008cef;
}

.phone__list {
  display: none;
}

@media screen and (min-width: 768px) {
