.navigation__link {
  letter-spacing: .01em;
  color: var(--main-text-color);
  font-size: 18px;
  font-weight: 500;
  line-height: 1.16;
  text-decoration: none;
}

.navigation__link:hover {
  color: var(--second-title-color);
  transition: color var(--transition-function);
}

.navigation__item {
  position: relative;
}

.navigation__item:hover, .navigation__item:focus {
