.services {
  display: inline-block;
  position: relative;
}

.services__button {
  width: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.services__button:hover, .services__button:focus {
  color: var(--second-title-color);
  transition: color var(--transition-function);
}

.services__drop {
  opacity: 0;
  width: 135px;
  height: 10%;
  background-color: rgba(0, 0, 0, 0);
  transition: transform .6s cubic-bezier(.4, 0, .2, 1), opacity .6s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: -35px;
  left: -30px;
  transform: translateY(0%);
}

.services__drop--link {
  letter-spacing: .01em;
  color: var(--main-text-color);
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.18;
}

.services__drop--link:hover, .services__drop--link:focus {
  color: var(--second-title-color);
  transition: color var(--transition-function);
}

.services__drop a {
  text-decoration: none;
}

.navigation__select--item:hover .services__drop, .navigation__select--item:focus .services__drop {
  opacity: 1;
  transform: translateY(2400%);
}

.language {
  display: flex;
  position: relative;
}

.language__select {
  cursor: pointer;
}

.language__variations {
  opacity: 0;
  width: 110%;
  height: 10%;
  background-color: rgba(0, 0, 0, 0);
  flex-direction: column;
  transition: transform .6s cubic-bezier(.4, 0, .2, 1), opacity .6s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(0);
}

